$primary-color: #4285f4;
$secondary-color: #3ddc84;

@keyframes preloader-inside-ring {
  0% {
    transform: scale(0, 0);
  }

  100% {
    transform: scale(1, 1);
  }
}

@keyframes preloader-inside-circle {
  0% {
    transform: scale(0, 0);
  }

  30% {
    transform: scale(0, 0);
  }

  100% {
    transform: scale(1, 1);
  }
}

.preloader {
  z-index: 10;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;
  padding: 20px;

  &__container {
    position: relative;
    width: 100px;
    height: 100px;
  }

  &__round {
    position: absolute;
    top: 0;
    left: 0;

    width: 100px;
    height: 100px;

    background: $primary-color;
    border-radius: 50px;
  }

  &__round::after,
  &__round::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    width: 100px;
    height: 100px;

    border-radius: 50px;
  }

  &__round::after {
    background: $secondary-color;
    animation: preloader-inside-ring 1s ease-in-out infinite;
  }

  &__round::before {
    z-index: 10;
    background: $primary-color;
    animation: preloader-inside-circle 1s ease-in-out infinite;
  }
}
